.n_container{
    padding:0;
    margin:0;
}
nav{
    z-index: 1;
    position: fixed;
    top: 0;
    width: 100%;
    height: 60px;
    background-color: #79ACDB;
    color: black;
    opacity: 85%; 
    ul{
        list-style:none;
        text-align: center;
        li{
            float: left;
            margin: 0 7px;
          //  padding: 5px;
            font-weight: bold;
            font-size: 13px;

            .n-btn, .n-btn:link, .n-btn:visited, .n-btn:active{
                border: none;
                font-size: 14px;
                background: #79ACDB;
                font-weight: 600;
                margin: 0 4px;
             }
             .n-btn:hover {
                 background: #79ACDB;
                 border: none;
                 border-bottom: 3px solid #d8a0a0;
                 font-size: 14px;
                 cursor:pointer;
                 font-weight: bold;
                 margin: 0 4px;
             }
        }
        
        
    }
}
